import {Button, Card, CardActions, CardContent, CardMedia, Container, Grid, Typography} from "@mui/material";
import {useEffect, useState} from "react";


interface PictureListProps {
    cards: number[];
}


export const PictureList = (props: PictureListProps) => {

    const [pictures, setPictures] = useState<any[]>([]);

    useEffect(() => {
        const Airtable = require('airtable');
        Airtable.configure({
            endpointUrl: 'https://api.airtable.com',
            apiKey: 'key3GiUe6QWKRxdmk'
        });
        const base = Airtable.base('appuECw2C7FL6JXKB');

        base('pictures').select({
            maxRecords: 15,
            view: "Grid view"
        }).eachPage(function page(records: any, fetchNextPage: any) {
            // This function (`page`) will get called for each page of records.
            console.log('records',records);
            let fetchedRecords : any = [];
            records.forEach(function(record: any) {
                console.log('Retrieved', record.get('Name'));
                console.log('Retrieved', record.get('Picture'));
                fetchedRecords.push({url:record.get('Picture')[0].url,name:record.get('Name')});
            });

            console.log('pictures',pictures);
            console.log('fetchedRecords',fetchedRecords);
            setPictures(fetchedRecords);

            // To fetch the next page of records, call `fetchNextPage`.
            // If there are more records, `page` will get called again.
            // If there are no more records, `done` will get called.
            fetchNextPage();

        }, function done(err: any) {
            if (err) { console.error(err); return; }
        });

    }, [])

    return (
        <>
            <Container sx={{ py: 8 }} maxWidth="md">
                <Grid container spacing={4}>
                    {pictures.map((element) => (
                        <Grid item key={element.name} xs={12} sm={6} md={4}>
                            <Card
                                sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}
                            >
                                <CardMedia
                                    component="img"
                                    // sx={{
                                    //     // 16:9
                                    //     pt: '56.25%',
                                    // }}
                                    image={element.url}
                                    alt="random"
                                />
                                <CardContent sx={{ flexGrow: 1 }}>
                                    <Typography gutterBottom variant="h5" component="h2">
                                        {element.name}
                                    </Typography>
                                    <Typography>
                                        {/*This is a media card. You can use this section to describe the*/}
                                        {/*content.*/}
                                    </Typography>
                                </CardContent>
                                <CardActions>
                                    <Button size="small">View</Button>
                                    <Button size="small">Edit</Button>
                                </CardActions>
                            </Card>
                        </Grid>
                    ))}
                </Grid>
            </Container>
        </>
    )
}